.classroom-item {
    cursor: pointer;
    font-size: 1rem;
    display: -ms-flexbox;
    display: flex;
    padding-top: 8px;
    padding-bottom: 8px;
  }
  
  .classroom-item:not(:last-child) {
    border-bottom: 1px solid #EEEEEE
  }
  
  .round-panel {
    background: #FFFFFF;
    width: 100%;
    padding: 16px;
    border-radius: 8px;
  }

  .classroom-item .no {
    -ms-flex-preferred-size: 5%;
        flex-basis: 5%;
    max-width: 60px;
  }
  
  .classroom-item .name {
    -ms-flex-preferred-size: 30%;
        flex-basis: 30%;
  }
  .classroom-item .name2{
    -ms-flex-preferred-size: 30%;
        flex-basis: 30%;
  }
  
  .classroom-item .name3{
    -ms-flex-preferred-size: 30%;
        flex-basis: 30%;
  }
  
  .classroom-item .video-count {
    -ms-flex-preferred-size: 20%;
        flex-basis: 20%;
  }
  
  .classroom-item .controls {
    -ms-flex-preferred-size: 5%;
        flex-basis: 5%;
    opacity: 1;

  }
  
  .classroom-item:hover {
    cursor: pointer;  
  }
  
  .classroom-item .delete {
    visibility: hidden;
  }
  
  .classroom-item:hover .delete {
    visibility: visible;
  }

  .not-opacity{
    opacity: 1;
  }

  .opacity-lock{
    opacity: 1;
  }


